import { Action } from '@ali/4ever-cangjie';
import Table from '../mo/models';

export const TABLE_SET_MIN_COLS_WIDTH_DATA = Symbol('TABLE_SET_MIN_COLS_WIDTH_DATA');

export function setTableMinColsWidthData(data: Record<string, number>) {
  return {
    type: TABLE_SET_MIN_COLS_WIDTH_DATA,
    payload: {
      data,
    },
  };
}

interface TableSetTableMinColsWidthDataPayload {
  data: Record<string, number>;
}

interface TableSetTableMinColsWidthDataAction {
  type: typeof TABLE_SET_MIN_COLS_WIDTH_DATA;
  payload: TableSetTableMinColsWidthDataPayload;
}

export function isTableSetTableMinColsWidthDataAction(
  action: Action,
): action is TableSetTableMinColsWidthDataAction {
  return action.type === TABLE_SET_MIN_COLS_WIDTH_DATA;
}

export const TABLE_ADJUST_COLS_WIDTH = Symbol('TABLE_ADJUST_COLS_WIDTH');

export function adjustColsWidth(node: Table, containerWidth?: number) {
  return {
    type: TABLE_ADJUST_COLS_WIDTH,
    payload: {
      node,
      containerWidth,
    },
  };
}

interface TableAdjustColsWidthPayload {
  node: Table;
  containerWidth?: number;
}

interface TableAdjustColsWidthAction {
  type: typeof TABLE_ADJUST_COLS_WIDTH;
  payload: TableAdjustColsWidthPayload;
}

export function isTableAdjustColsWidthAction(
  action: Action,
): action is TableAdjustColsWidthAction {
  return action.type === TABLE_ADJUST_COLS_WIDTH;
}
